<template>
  <the-header></the-header>
  <div
    :class="smallScreen ? 'column' : 'row'"
    :style="smallScreen ? 'height: 96vh; width: 100vw; padding-top:0px' : ''"
  >
    <div class="print-hide">
      <q-stepper
        class="test"
        v-model="step"
        header-nav
        ref="stepper"
        color="primary"
        animated
      >
        <q-step
          :name="1"
          title="Questions"
          icon="settings"
          :done="step > 1"
          :header-nav="step > 1"
          class="management-tech-step-wrap step-1-wrap"
        >
          <div
            :class="smallScreen ? '' : 'col-6 q-pa-md'"
            style="padding-right: 0px; padding-top: 1px; display: flex; padding: 1px 0px 15px 0px;"
          >
            <div
              :style="
                smallScreen
                  ? 'width: 100%'
                  : 'width:500px; padding-right: 0px; margin-bottom: 70px;'
              "
            >
              <app-info
                class="q-pa-md app-info"
                v-show="smallScreen"
              ></app-info>
              <the-user-questions :answers="answers"></the-user-questions>
            </div>
            <q-stepper-navigation class="report-stepper-nav bg-white">
              <q-btn
                :disable="questionsComplete"
                @click="
                  () => {
                    done1 = true;
                    step = 2;
                  }
                "
                color="primary"
                label="Continue"
              />
              <div
                v-if="questionsComplete"
                class="q-ml-md text-primary text-caption "
              >
                Answer questions to enable
              </div>
            </q-stepper-navigation>
          </div>
        </q-step>

        <q-step
          :name="2"
          title="Define Area"
          icon="map"
          :done="step > 2"
          :header-nav="step > 2"
          class="management-tech-step-wrap"
          :class="smallScreen ? 'step-23-wrap' : ''"
        >
          <div
            :class="smallScreen ? 'col-5' : 'col-3 q-pa-md'"
            style="padding-right: 0px; padding-top: 1px;"
          >
            <div
              :style="
                smallScreen
                  ? 'margin: auto; height: calc(100vh - 140px)'
                  : 'width:500px; padding-right: 15px; margin: auto; height: calc(100vh - 140px)'
              "
            >
              <the-defined-area></the-defined-area>
            </div>
            <q-stepper-navigation
              v-if="!smallScreen"
              class="report-stepper-nav bg-white"
            >
              <q-btn
                @click="
                  () => {
                    done2 = true;
                    step = 3;
                  }
                "
                color="primary"
                label="Continue"
                :disable="disableBtn"
              />
              <q-btn
                flat
                @click="step = 1"
                color="primary"
                label="Back"
                class="q-ml-sm"
              />
            </q-stepper-navigation>
          </div>
        </q-step>

        <q-step
          :name="3"
          title="Report"
          icon="description"
          :header-nav="step > 3"
          class="management-tech-step-wrap"
          :class="smallScreen ? 'step-23-wrap' : ''"
        >
          <div
            :class="smallScreen ? 'col-5' : 'col-5 q-pa-md'"
            style="padding: 0px 15px 0px 15px; height: calc(100vh - 120px);"
          >
            <div
              :style="
                smallScreen
                  ? 'margin-bottom: 70px;'
                  : 'width: 600px; padding-bottom: 70px;'
              "
            >
              <the-management-techniques
                :answers="answers"
                :indicators="indicators"
              ></the-management-techniques>
            </div>
            <q-stepper-navigation
              v-if="!smallScreen"
              class="report-stepper-nav bg-white"
            >
              <q-btn color="primary" @click="startPdf()" label="Save Report" />
              <q-btn
                flat
                @click="step = 2"
                color="primary"
                label="Back"
                class="q-ml-sm"
              />
            </q-stepper-navigation>
          </div>
        </q-step>
      </q-stepper>
    </div>
    <!-- Application Info -->
    <div v-show="step == 1 && !smallScreen" class="col">
      <app-info class="app-info"></app-info>
    </div>
    <div
      v-show="step > 1"
      :class="smallScreen ? 'col print-hide' : 'col print-hide'"
      :style="smallScreen ? '' : 'padding-top:1px;'"
    >
      <the-map></the-map>
      <q-stepper-navigation
        v-if="smallScreen && step == 2"
        class="report-stepper-nav bg-white"
      >
        <q-btn
          @click="
            () => {
              done2 = true;
              step = 3;
            }
          "
          color="primary"
          label="Continue"
          :disable="disableBtn"
        />
        <q-btn
          flat
          @click="step = 1"
          color="primary"
          label="Back"
          class="q-ml-sm"
        />
      </q-stepper-navigation>
      <q-stepper-navigation
        v-if="smallScreen && step == 3"
        class="report-stepper-nav bg-white"
      >
        <q-btn color="primary" label="Save Report" />
        <q-btn
          flat
          @click="step = 2"
          color="primary"
          label="Back"
          class="q-ml-sm"
        />
      </q-stepper-navigation>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue';
import TheMap from './components/UI/TheMap.vue';
import TheHeader from './components/UI/TheHeader.vue';
import TheUserQuestions from './components/UI/TheUserQuestions.vue';
import TheManagementTechniques from './components/UI/TheManagementTechniques.vue';
import TheDefinedArea from './components/UI/TheDefinedArea.vue';
import AppInfo from './components/UI/AppInfo.vue';
import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
import htmlToPdfmake from 'html-to-pdfmake';

export default {
  data() {
    return {
      publicPath: process.env.BASE_URL,
    };
  },
  name: 'App',
  components: {
    TheMap,
    TheHeader,
    TheUserQuestions,
    TheManagementTechniques,
    TheDefinedArea,
    AppInfo,
    //TheMapToggle, TheSideNav
  },
  computed: {
    smallScreen() {
      return this.$q.screen.lt.sm;
    },
    answers() {
      return this.$store.state.answers;
    },
    indicators() {
      return this.$store.state.indicators;
    },
    techniques() {
      return this.$store.state.techniques;
    },
    indicatorXtechniques() {
      return this.$store.state.indicatorXtechniques;
    },
    techniqueCount() {
      return this.$store.state.techniqueCount;
    },
    otherTechniqueCount() {
      return this.$store.state.otherTechniqueCount;
    },
    otherConsiderations() {
      return this.$store.state.otherConsiderations;
    },
    questionsComplete() {
      if (
        this.$store.state.answers.q1 != '' &&
        this.$store.state.answers.q2 != '' &&
        this.$store.state.answers.q3 != '' &&
        this.$store.state.answers.q4 != '' &&
        this.$store.state.answers.q5 != ''
      ) {
        return false;
      } else {
        return true;
      }
    },
    disableBtn() {
      if (this.$store.state.reportReady) {
        return false;
      } else {
        return true;
      }
    },
    printMap: {
      get() {
        return this.$store.state.printMap;
      },
      set(value) {
        this.$store.commit('updatePrintMap', value);
      },
    },
    step: {
      get() {
        return this.$store.state.stepper;
      },
      set(value) {
        this.$store.commit('updateStepper', value);
      },
    },
    mapPrintURI() {
      return this.$store.state.mapPrintURI;
    },
    scaleValue() {
      return this.$store.state.scaleValue;
    },
    foundIndicators() {
      return this.$store.state.foundIndicators;
    },
  },
  watch: {
    mapPrintURI() {
      this.generatePdf();
    },
  },
  mounted() {
    this.$q.screen.setSizes({ sm: 700 });
  },
  methods: {
    startPdf() {
      this.printMap = true;
    },
    async generatePdf() {
      // Set the fonts for pdfmake
      pdfMake.vfs = pdfFonts.pdfMake.vfs;

      // Get top techniques
      let topTechnique = '<ul>';
      this.techniqueCount.forEach((t) => {
        let rc = '';
        let bc = '';
        if (t.resilience === true) {
          rc = '<span style="color:#FFC000;">&#82;</span>';
        }
        if (t.blue === true) {
          bc = '<span style="color:#4cb0cf;">&#66;</span>';
        }
        topTechnique += '<li>' + t.label + ' ' + rc + ' ' + bc + '</li>';
      });
      topTechnique += '</ul>';
      var topTechniqueHtml = htmlToPdfmake(topTechnique);
      // Get other top techniques
      let otherTechnique = '<ul>';
      this.otherTechniqueCount.forEach((t) => {
        otherTechnique += '<li>' + t.label + '</li>';
      });
      otherTechnique += '</ul>';
      var otherTechniqueHtml = htmlToPdfmake(otherTechnique);
      let creditIcons = htmlToPdfmake(
        '<b><span style="color:#FFC000;">&#82;</span></b>: May qualify for Resilience Credits.<br /><b><span style="color:#4cb0cf;">&#66;</span></b>: May qualify for Blue Carbon Credits.'
      );
      let foundIndicator = '';
      let absentIndicator = '<ul>';
      this.foundIndicators.forEach((a) => {
        console.log(this.publicPath);
        foundIndicator +=
          '<div class="header3" style="margin-top:10px;">' +
          a.label +
          '</div><div style="text-decoration:underline; margin:5px;">Recommended Management Techniques</div><ul>';
        a.techniques.forEach((t) => {
          let imgSrc = '';
          let show = '';
          if (t.resilience && t.blue) {
            show = 'both';
          } else {
            if (t.resilience) {
              show = 'resilience';
            }
            if (t.blue) {
              show = 'blue';
            }
          }
          if (show === 'resilience') {
            imgSrc = '<span style="color:#FFC000;">&#82;</span>';
          }
          if (show === 'blue') {
            imgSrc = '<span style="color:#4cb0cf;">&#66;</span>';
          }
          if (show === 'both') {
            imgSrc =
              '<span style="color:#FFC000;">&#82;</span> <span style="color:#4cb0cf;">&#66;</span>';
          }
          foundIndicator += '<li>' + t.label + ' ' + imgSrc + '</li>';
        });
        foundIndicator += '</ul>';
      });
      this.indicatorXtechniques.forEach((ind) => {
        if (this.indicators[ind.field] === 'No') {
          absentIndicator += '<li>' + ind.label + '</li>';
        }
      });
      absentIndicator += '</ul>';
      var foundHtml = htmlToPdfmake(foundIndicator);
      var absentHtml = htmlToPdfmake(absentIndicator);
      var today = new Date();
      var dateString = today.toLocaleDateString('en-US', {
        weekday: 'long',
        year: 'numeric',
        month: 'long',
        day: 'numeric',
      });
      // Define the content of the PDF document
      var docDefinition = {
        header: {
          text: dateString,
          alignment: 'right',
          margin: [0, 20, 20, 0],
        },
        footer: function(currentPage, pageCount) {
          return {
            text:
              'Page ' + currentPage.toString() + ' of ' + pageCount.toString(),
            alignment: 'center',
            margin: [0, 0, 0, 10],
          };
        },
        content: [
          {
            text: 'Marsh Management Decision Tool Report',
            style: ['header1', 'centerItem'],
            margin: [0, 0, 0, 20],
          },
          {
            text: [
              'This report was generated using the Marsh Management Decision Tool and provides recommended marsh management techniques. The ',
              { text: 'User Questions', style: 'boldItem' },
              ' and ',
              { text: 'User-Defined Work Area', style: 'boldItem' },
              ' were used to evaluate publicly available data sets of the Chesapeake Bay region for marsh health indicators and metrics. If the indicators and metrics were found to occur within the User-defined Work Area, a list of recommended marsh management techniques were provided for each indicator and metric.',
            ],
            style: 'header4',
            margin: [0, 0, 0, 20],
          },
          {
            columns: [
              [
                {
                  text: 'User Questions',
                  style: 'header2',
                  margin: [0, 0, 0, 0],
                },
                {
                  ol: [
                    {
                      text: [
                        'Are ghost forest present in your marsh area? \n',
                        { text: this.answers.q1, style: 'boldItem' },
                      ],
                      margin: [0, 0, 0, 3],
                    },
                    {
                      text: [
                        'Are there currently culverts within the marsh? \n',
                        { text: this.answers.q2, style: 'boldItem' },
                      ],
                      margin: [0, 0, 0, 3],
                    },
                    {
                      text: [
                        'Are there any dams present upstream of your watershed? \n',
                        { text: this.answers.q3, style: 'boldItem' },
                      ],
                      margin: [0, 0, 0, 3],
                    },
                    {
                      text: [
                        'Are there ditches with plugs in your marsh? \n',
                        { text: this.answers.q4, style: 'boldItem' },
                      ],
                      margin: [0, 0, 0, 3],
                    },
                    {
                      text: [
                        'Are there impoundments present in your marsh? \n',
                        { text: this.answers.q5, style: 'boldItem' },
                      ],
                    },
                  ],
                  margin: [0, 5, 5, 5],
                },
              ],
              [
                {
                  text: 'User-Defined Work Area',
                  style: 'header2',
                  margin: [0, 0, 0, 5],
                },
                {
                  image: this.mapPrintURI,
                  width: 250,
                  height: 150,
                },
                {
                  text: this.scaleValue,
                  alignment: 'right',
                  margin: [0, 5, 10, 5],
                },
              ],
            ],
          },
          {
            text: 'Recommended Management Techniques',
            style: ['header1a', 'centerItem'],
            margin: [0, 20, 0, 10],
          },
          {
            text:
              'Management techniques that were recommended most often across marsh health indicators based on the user questions and the available data for the defined area of work. The number following the recommended technique indicates the number of times that specific technique is recommended based on the number of found indicators in your marsh area.',
            style: 'header4',
            margin: [0, 0, 0, 10],
          },
          {
            text: creditIcons,
            alignment: 'bottom',
            margin: [0, 0, 0, 10],
          },
          topTechniqueHtml,
          {
            text: 'Other management techniques suggested:',
            style: 'header4',
            margin: [0, 5, 0, 5],
          },
          otherTechniqueHtml,
          {
            text: 'Found Indicators',
            style: ['header1a', 'centerItem'],
            margin: [0, 20, 0, 10],
            pageBreak: 'before',
          },
          {
            text:
              'The following is a list of all indicators, and their recommended  management techniques, that were either found within the User-Defined Work Area or included based on the answers to the User Questions.',
            style: 'header4',
            margin: [0, 0, 0, 10],
          },
          foundHtml,
          {
            text: 'Absent Indicators',
            style: ['header1a', 'centerItem'],
            margin: [0, 20, 0, 10],
          },
          {
            text:
              'The following is a list of indicators and metrics that were either not found within the User-Defined Work Area or not included based on the answers to the User Questions.',
            style: 'header4',
            margin: [0, 0, 0, 5],
          },
          absentHtml,
          {
            text:
              'For more information on the management strategies and techniques, review or print any of the following informational PDF documents:',
            margin: [0, 5, 0, 5],
          },
          {
            ul: [
              {
                text: 'Shoreline Protection',
                link:
                  'https://maps.tnc.org/marsh-management/1._Shoreline_Protection_One_Pager.pdf',
                decoration: 'underline',
              },
              {
                text: 'Water Quality Improvements',
                link:
                  'https://maps.tnc.org/marsh-management/2._Water_Quality_Improvements_One_Pager.pdf',
                decoration: 'underline',
              },
              {
                text: 'Sediment Management',
                link:
                  'https://maps.tnc.org/marsh-management/3._Sediment_Management_One_Pager.pdf',
                decoration: 'underline',
              },
              {
                text: 'Invasive Species Removal',
                link:
                  'https://maps.tnc.org/marsh-management/4._Invasive_Species_Removal_One_Pager.pdf',
                decoration: 'underline',
              },
              {
                text: 'Improve Hydrological Connectivity',
                link:
                  'https://maps.tnc.org/marsh-management/5._Improve_Hydrological_Connectivity_One_Pager.pdf',
                decoration: 'underline',
              },
              {
                text: 'Vegetation Management',
                link:
                  'https://maps.tnc.org/marsh-management/6._Vegetation_Management.pdf',
                decoration: 'underline',
              },
              {
                text: 'Endangered Species Management',
                link:
                  'https://maps.tnc.org/marsh-management/7._Endangered_Species_Management_One_Pager.pdf',
                decoration: 'underline',
              },
              {
                text: 'Marsh Migration Buffers',
                link:
                  'https://maps.tnc.org/marsh-management/8._Marsh_Migration_Buffers_One_Pager.pdf',
                decoration: 'underline',
              },
            ],
            color: '#7393B3',
            margin: [5, 5, 0, 0],
          },
          {
            text:
              'This material is based on work supported by the U.S. Environmental Protection Agency (Assistance Agreement No. CB96358101) and the National Fish and Wildlife Foundation’s Chesapeake Bay Stewardship Fund, which promotes community-based efforts to develop conservation strategies to protect and restore the diverse natural resources of the Chesapeake Bay.',
            margin: [0, 20, 0, 10],
          },
          {
            text:
              'Disclaimer: The views and conclusions contained in this document are those of the authors and should not be interpreted as representing the opinions or policies of the U.S. Government or the National Fish and Wildlife Foundation and its funding sources. Mention of trade names or commercial products does not constitute their endorsement by the U.S. Government, or the National Fish and Wildlife Foundation or its funding sources.',
            margin: [0, 5, 0, 5],
          },
        ],
        defaultStyle: {
          fontSize: 11,
          color: '#374269',
        },
        styles: {
          header1: {
            bold: true,
            fontSize: 18,
          },
          header1a: {
            bold: true,
            fontSize: 16,
          },
          header2: {
            bold: true,
            fontSize: 14,
          },
          header3: {
            bold: true,
            fontSize: 12,
          },
          header4: {
            fontSize: 12,
          },
          anotherStyle: {
            italics: true,
            alignment: 'right',
          },
          boldItem: {
            bold: true,
          },
          centerItem: {
            alignment: 'center',
          },
        },
      };

      // Create a PDF document
      pdfMake.createPdf(docDefinition).download();
    },
    getDate() {
      let formattedDate = new Date().toLocaleString('en-us', {
        weekday: 'long',
        year: 'numeric',
        month: 'short',
        day: 'numeric',
      });
      return formattedDate;
    },
    updateCondensedTabs(value) {
      //this function updates the tab state (condensed true/false) to show icon only
      if (value < 150 && !this.$store.state.condensedTabs) {
        this.$store.commit('updateCondensedTabs', true);
      } else if (value > 150 && this.$store.state.condensedTabs) {
        this.$store.commit('updateCondensedTabs', false);
      }
      this.$store.commit('updateContainerWidth', value);
    },
    updateScrollContainerHeight(value) {
      //this function updates the height of the scroll container (tab panels) in mobile view

      //todo: notate what does the 150 control
      let newVal = value;
      //document.getElementById('panelM').style.height = 'calc(100vh - ' + newVal  + 'px)'
      document.getElementsByClassName('panelM').forEach((elem) => {
        elem.style.height = 'calc(100vh - ' + newVal + 'px)';
      });
    },
    openSupLayers() {
      this.$store.commit('updateShowLayers');
    },
  },
  updateAvailable(event) {
    this.registration = event.detail;
    this.updateExists = true;
  },
  refreshApp() {
    this.updateExists = false;
    // Make sure we only send a 'skip waiting' message if the SW is waiting
    if (!this.registration || !this.registration.waiting) return;
    // Send message to SW to skip the waiting and activate the new SW
    this.registration.waiting.postMessage({ type: 'SKIP_WAITING' });
  },
};
</script>

<style>
body {
  font-size: 16px !important;
}
.content-wrap {
  height: calc(100vh - 50px);
}
.q-stepper__header--border {
  box-shadow: 0 2px 2px -2px rgb(51 51 51);
}
.esri-view-width-xsmall .esri-expand--auto .esri-expand__mask--expanded {
  display: none;
}
.report-stepper-nav {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  text-align: center;
  padding: 0px 0 18px 10px;
  box-shadow: 0 2px 2px 2px rgb(51 51 51);
}
.step-1-wrap {
  height: calc(100vh - 124px) !important;
}
.step-23-wrap {
  height: calc(100vh - 455px) !important;
}
.management-tech-step-wrap .q-stepper__nav {
  padding-top: 18px;
}
.q-stepper--horizontal .q-stepper__step-inner {
  padding: 0 !important;
}
.app-info {
  background-image: url('/marsh-management/MapBack.jpg');
  /* background-image: url('/MapBack.jpg'); */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 35px;
  color: #374269;
  height: calc(100vh - 50px);
  font-weight: bolder;
  text-shadow: 1px 1px 5px #898e9f;
  overflow-y: scroll;
  font-size: 17px;
}
.smallMap {
  height: 200px;
}
.text-h6 {
  font-weight: 400 !important;
}
@media screen and (max-width: 700px) {
  .esri-view-width-xsmall .esri-expand--auto .esri-expand__mask--expanded {
    display: none;
  }
  .esri-view-width-xsmall .esri-expand--auto .esri-expand__container--expanded {
    top: 100px;
  }
  .esri-view-width-xsmall
    .esri-expand--auto
    .esri-expand__container--expanded
    .esri-expand__panel {
    padding: 2px;
  }
  .app-info {
    height: auto;
  }
}
</style>
