    provides recommended marsh management techniques. The User Questions and
    User-Defined Work Area were used to evaluate publicly available data sets of
    the Chesapeake Bay region for marsh health indicators and metrics. If the
    indicators and metrics were found to occur within the User-defined Work
    Area, a list of recommended marsh management techniques were provided for
