        <li>
          {{ tech.label }}
          <icon-button v-if="tech.resilience" type="resilience"></icon-button>
          <icon-button v-if="tech.blue" type="blue"></icon-button>
        </li>
      </ul>
    </div>
  </div>
  <br />
