<script>
import IconButton from './IconButton.vue';
import LayerCheckbox from './LayerCheckbox.vue';

export default {
  data() {
    return {
      indInfo: false,
      nuisanceInfo: false,
      developmentInfo: false,
      ditchesInfo: false,
      erosionInfo: false,
      sedimentInfo: false,
      waterInfo: false,
      fragInfo: false,
      elevationInfo: false,
      slopeInfo: false,
      shorelineInfo: false,
      poolsInfo: false,
      invasiveInfo: false,
      publicPath: process.env.BASE_URL,
    };
  },
  components: { LayerCheckbox, IconButton },
  props: ['answers', 'indicators'],
  methods: {
    indicatorInfo(val) {
      console.log(val);
      if (this.indInfo === false) {
        this.indInfo = true;
        if (val.label === 'Nuisance Flooding') {
          this.nuisanceInfo = true;
        } else if (val.label === 'Development/Land Use') {
          this.developmentInfo = true;
        } else if (val.label === 'Ditches') {
          this.ditchesInfo = true;
        } else if (val.label === 'Erosion') {
          this.erosionInfo = true;
        } else if (val.label === 'Sediment Availability') {
          this.sedimentInfo = true;
        } else if (val.label === 'Water Quality') {
          this.waterInfo = true;
        } else if (val.label === 'Fragmentation') {
          this.fragInfo = true;
        } else if (val.label === 'Elevation Vulnerability') {
          this.elevationInfo = true;
        } else if (val.label === 'Slope') {
          this.slopeInfo = true;
        } else if (val.label === 'Horizontal Shoreline Change') {
          this.shorelineInfo = true;
        } else if (val.label === 'Marsh Density/Pools and Ponds') {
          this.poolsInfo = true;
        } else if (val.label === 'Invasive Species') {
          this.invasiveInfo = true;
        }
      }

      console.log(this.indInfo);
      console.log(this.nuisanceInfo);
      console.log(this.developmentInfo);
    },
    removeInfo() {
      if (this.indInfo === true) {
        this.indInfo = false;
        this.nuisanceInfo = false;
        this.developmentInfo = false;
        this.ditchesInfo = false;
        this.erosionInfo = false;
        this.sedimentInfo = false;
        this.waterInfo = false;
        this.fragInfo = false;
        this.elevationInfo = false;
        this.slopeInfo = false;
        this.shorelineInfo = false;
        this.poolsInfo = false;
        this.invasiveInfo = false;
      }
    },
  },
  computed: {
    foundIndicators() {
      return this.$store.state.foundIndicators;
    },
    absentIndicators() {
      return this.$store.state.absentIndicators;
    },
    techniqueCount() {
      return this.$store.state.techniqueCount;
    },
    otherTechniqueCount() {
      return this.$store.state.otherTechniqueCount;
    },
    otherConsiderations() {
      return this.$store.state.otherConsiderations;
    },
    mapScale() {
      return this.$store.state.mapScale;
    },
  },
};
</script>

