    <div class="text-h4 text-weight-bolder text-center">
      Chesapeake Bay Marsh Management Decision Tool
    </div>
    <div class="q-mt-md text-h6">About</div>
    <div>
      The Marsh Management Decision Tool is a guidance system that provides the
      user with recommended marsh management techniques. The recommendations are
      based on evaluating publicly available data sets for the Chesapeake Bay
      region for marsh health indicators and metrics. The marsh health
      indicators included in this decision tool are nuisance flooding,
      development, ditches, erosion, sediment availability, water quality,
      fragmentation, elevation, slope, horizontal shoreline position, edge type,
      marsh density, pools/ponds, stormwater runoff, water level, native
      vegetation, invasive species, sensitive species, and ghost forest. Many of
      the indicators are inversely related to marsh health (e.g., high density
      of development and poor marsh health), while others are directly related
      (e.g., high sediment availability is correlated with higher marsh health).
      This management decision support tool has been designed to provide
      information for the Chesapeake Bay marshes (Maryland and Virginia, United
      States) and to increase the user’s ability to plan and implement
      appropriate marsh management.
    </div>
    <div>
      This material is based on work supported by the U.S. Environmental
