    <div class="q-mt-md text-h6">How to Use</div>
    <div>
      The tool will analyze the available information on marsh health indicators
      for your area of interest, based on your answer to the questions and
      publicly available data. When the assessment is complete, a summary window
      with the recommended strategies will open, and you can download a summary
      report. The estimated time to use the tool is 5-10 minutes.
    </div>
