<template>
  <div class="q-gutter-sm">
    <q-radio
      label="Yes/Uncertain"
      v-model="yesno"
      val="Yes"
      size="sm"
      @update:model-value="radioUpdate"
    />
    <q-radio
      label="No"
      v-model="yesno"
      val="No"
      size="sm"
      @update:model-value="radioUpdate"
    />
  </div>
</template>
<script>
export default {
  props: ['question', 'answer'],
  data() {
    return {
      yesno: this.answer,
    };
  },
  methods: {
    radioUpdate(val) {
      if (this.question == 'q1') {
        this.$store.commit('updateIndicators', ['coastalForest', val]);
      }
      this.$store.commit('updateAnswers', [this.question, val]);
    },
  },
};
</script>
