<template>
  <div
    id="map"
    :style="smallScreen ? 'height: 73% ' : 'height: calc(100vh - 50px);'"
  >
    <div id="sketchTools"></div>
    <div id="scaleWarning" v-if="mapScale && stepper === 3">
      <q-card
        class="q-py-sm q-px-md bg-red-3"
        style="font-size: 13px; text-align: center;"
        >Zoom in to enable checkboxes <br />and view indicator layers</q-card
      >
    </div>
  </div>
</template>

<script>
import Map from '@arcgis/core/Map';
import MapView from '@arcgis/core/views/MapView';
import MapImageLayer from '@arcgis/core/layers/MapImageLayer';
import Legend from '@arcgis/core/widgets/Legend';
import Measurement from '@arcgis/core/widgets/Measurement';
import Expand from '@arcgis/core/widgets/Expand';
import PortalSource from '@arcgis/core/widgets/BasemapGallery/support/PortalBasemapsSource';
import BasemapGallery from '@arcgis/core/widgets/BasemapGallery';
import FeatureLayer from '@arcgis/core/layers/FeatureLayer';
import ScaleBar from '@arcgis/core/widgets/ScaleBar';
import esriRequest from '@arcgis/core/request';
import Graphic from '@arcgis/core/Graphic';
import Field from '@arcgis/core/layers/support/Field';
import Sketch from '@arcgis/core/widgets/Sketch';
import GraphicsLayer from '@arcgis/core/layers/GraphicsLayer';
import Portal from '@arcgis/core/portal/Portal';
import PortalBasemapsSource from '@arcgis/core/widgets/BasemapGallery/support/PortalBasemapsSource';
import ActionButton from '@arcgis/core/support/actions/ActionButton.js';

//global in order to have access to the maplayer
let esri = {
  mapView: '',
  modelLayer: '',
  supportingMapLayer: '',
  legend: '',
  map: '',
  measurement: '',
  lgExpand: '',
  sketch: '',
  graphicsLayer: '',
  createEvent: false,
  shapefileLayers: '',
  sublayers: [
    {
      id: 1,
      visible: false,
      title: 'Nuisance Flooding',
      opacity: 0.5,
    },
    {
      id: 2,
      visible: false,
      title: 'Impervious Surface',
      opacity: 0.5,
    },
    {
      id: 3,
      visible: false,
      title: 'Ditch Metric',
      opacity: 0.5,
    },
    {
      id: 4,
      visible: false,
      title: 'Erosion',
      opacity: 0.5,
    },
    {
      id: 5,
      visible: false,
      title: 'Total Suspended Sediments',
      opacity: 0.5,
    },
    {
      id: 6,
      visible: false,
      title: 'Water Pollution',
      opacity: 0.5,
    },
    {
      id: 7,
      visible: false,
      title: 'Wetland Local Connectedness Value',
      opacity: 0.5,
    },
    {
      id: 8,
      visible: false,
      title: 'Slope',
      opacity: 0.5,
    },
    {
      id: 9,
      visible: false,
      title: 'Marina',
      opacity: 0.5,
    },
    {
      id: 10,
      visible: false,
      title: 'MD Marina',
      opacity: 0.5,
    },
    {
      id: 11,
      visible: false,
      title: 'VA Marina',
      opacity: 0.5,
    },
    {
      id: 12,
      visible: false,
      title: 'Bulkhead',
      opacity: 0.5,
    },
    {
      id: 13,
      visible: false,
      title: 'MD Bulkhead',
      opacity: 0.5,
    },
    {
      id: 14,
      visible: false,
      title: 'VA Bulkhead',
      opacity: 0.5,
    },
    {
      id: 15,
      visible: false,
      title: 'Horizontal Shoreline Position',
      opacity: 0.5,
    },
    {
      id: 16,
      visible: false,
      title: 'VA EPR 1937 and 2009',
      opacity: 0.5,
    },
    {
      id: 17,
      visible: false,
      title: 'VA EPR 1937/38 and 2017',
      opacity: 0.5,
    },
    {
      id: 18,
      visible: false,
      title: 'MD Horizontal Shoreline Position',
      opacity: 0.5,
    },
    {
      id: 19,
      visible: false,
      title: 'UVVR Value',
      opacity: 0.5,
    },
    {
      id: 20,
      visible: false,
      title: 'Invasive Species',
      opacity: 0.5,
    },
    {
      id: 21,
      visible: false,
      title: 'Invasive Species',
      opacity: 0.5,
    },
    {
      id: 22,
      visible: false,
      title: 'Invasive Species',
      opacity: 0.5,
    },
    {
      id: 23,
      visible: false,
      title: 'Saltmarsh Sparrow Habitat',
      opacity: 0.5,
    },
    {
      id: 24,
      visible: false,
      title: 'Terrapin Habitat',
      opacity: 0.5,
    },
    {
      id: 25,
      visible: false,
      title: 'Submerged Aquatic Vegetation (SAV)',
      opacity: 0.5,
    },
    {
      id: 26,
      visible: false,
      title: 'Benthic Habitat',
      opacity: 0.5,
    },
    {
      id: 27,
      visible: false,
      title: 'Elevation Vulnerability',
      opacity: 0.5,
    },
    {
      id: 28,
      visible: true,
      title: 'Marshes',
      opacity: 0.7,
    },
  ],
};

export default {
  name: 'TheMap',
  data() {
    return {
      active: true,
    };
  },
  computed: {
    smallScreen() {
      return this.$q.screen.lt.sm;
    },
    supportingMapVisibleLayers() {
      //returns list of all ticked objects [{mapService: index in config, id: layerid, type: type}, ...]
      return this.$store.state.tree.tickedObj;
    },
    supportingVisibleLayerOpacity() {
      //returns object {value: OpacVal, id: Layerid}
      return this.$store.state.supportingVisibleLayerOpacity;
    },
    clearShapefile() {
      return this.$store.state.clearShapefile;
    },
    startSketch() {
      return this.$store.state.startSketch;
    },
    uploadFileName() {
      return this.$store.state.uploadFileName;
    },
    queryDatasets: {
      get() {
        return this.$store.state.queryDatasets;
      },
      set(value) {
        this.$store.commit('updateQueryDatasets', value);
      },
    },
    creatingReport: {
      get() {
        return this.$store.state.creatingReport;
      },
      set(value) {
        this.$store.commit('updateCreatingReport', value);
      },
    },
    reportReady: {
      get() {
        return this.$store.state.reportReady;
      },
      set(value) {
        this.$store.commit('updateReportReady', value);
      },
    },
    showStatus: {
      get() {
        return this.$store.state.showStatus;
      },
      set(value) {
        this.$store.commit('updateShowStatus', value);
      },
    },
    largeArea: {
      get() {
        return this.$store.state.largeArea;
      },
      set(value) {
        this.$store.commit('updateLargeArea', value);
      },
    },
    smallArea: {
      get() {
        return this.$store.state.smallArea;
      },
      set(value) {
        this.$store.commit('updateSmallArea', value);
      },
    },
    outsideArea: {
      get() {
        return this.$store.state.outsideArea;
      },
      set(value) {
        this.$store.commit('updateOutsideArea', value);
      },
    },
    layerVis() {
      return this.$store.state.layerVis;
    },
    printMap() {
      return this.$store.state.printMap;
    },
    mapScale: {
      get() {
        return this.$store.state.mapScale;
      },
      set(value) {
        this.$store.commit('updateMapScale', value);
      },
    },
    stepper() {
      return this.$store.state.stepper;
    },
  },
  watch: {
    layerVis() {
      this.layerVisibility();
    },
    supportingMapVisibleLayers() {
      this.updateSupportingVisibility();
    },
    supportingVisibleLayerOpacity() {
      this.updateSupportingOpacity();
    },
    clearShapefile() {
      this.clearShapefilePoly();
    },
    startSketch() {
      this.sketchTools();
    },
    uploadFileName() {
      this.generateFeatureCollection();
    },
    showStatus() {
      if (!this.showStatus) {
        this.clearSketch();
      }
    },
    showLayers() {
      this.showSupportingLayers();
    },
    printMap() {
      if (this.printMap) {
        this.getMapPrint();
      }
    },
  },

  mounted() {
    // suppporting layers to turn on when report is complete
    esri.layers = new MapImageLayer({
      url:
        'https://services2.coastalresilience.org/arcgis/rest/services/Chesapeake/MarshManagement/MapServer',
      sublayers: esri.sublayers,
      title: 'Legend',
    });
    // grid to query with shapefile or draw tool
    esri.grid = new FeatureLayer({
      url:
        'https://services2.coastalresilience.org/arcgis/rest/services/Chesapeake/MarshManagement/MapServer/0',
      outFields: ['*'],
    });

    // Portal IDs for TNC Basemaps. Use any id to set the map's basemap.
    const tncHillshadeMapId = 'd22aed9a4acb4bc8ae8f2141732af496';
    const tncDarkMapId = '1f48b2b2456c44ad9c58d6741378c2ba';
    const oceansMapId = '67ab7f7c535c4687b6518e6d2343e8a2';
    const hybridMapId = '86265e5a4bbb4187a59719cf134e0018';

    //select a basemap
    esri.map = new Map({
      basemap: {
        portalItem: {
          id: tncHillshadeMapId,
        },
      },
      layers: [esri.layers],
    });

    // Add work area sublayer

    //create the map view
    esri.mapView = new MapView({
      map: esri.map,
      center: [-76.16712, 38.15795],
      zoom: 7,
      container: this.$el,
    });

    // Create portal. Used for PortalBasemapSource below
    const portal = new Portal();

    // Titles of TNC Basemaps to include in Basemap Gallery Widget
    const allowedBasemapTitles = [
      'Imagery Hybrid',
      'Ocean Basemap',
      'TNC Light with Hillshade',
      'TNC Dark Gray Map',
    ];

    // Define Basemap Gallery Wideget source using ID from TNC Basemap
    //  Gallery ArcGIS Online Group. By default, all maps from group will
    //  be added to the Basemap Gallery Widget. The filterFunction only
    //  brings in maps from the group whose titles match those in the
    //  allowedBasemapTitles array above.
    const source = new PortalBasemapsSource({
      portal,
      query: {
        id: 'defa1b2287604d069c70af515331e30f',
      },
      filterFunction: (basemap) =>
        allowedBasemapTitles.indexOf(basemap.portalItem.title) > -1,
    });

    // Create an expand widget to house the Basemap Gallery Widget. Also
    //  create the Basemap Gallery Widget using the source and view created
    //  above.
    let view = esri.mapView;
    const bgExpand = new Expand({
      view,
      content: new BasemapGallery({ source, view }),
      expandIconClass: 'esri-icon-basemap',
    });
    esri.mapView.ui.add(bgExpand, 'top-right');

    //code to add highlight feature for map image layer
    esri.mapView.popup.watch('selectedFeature', function(gra) {
      if (gra) {
        esri.mapView.graphics.removeAll();
        var h = esri.mapView.highlightOptions;
        gra.symbol = {
          type: 'simple-fill', // autocasts as new SimpleFillSymbol()
          color: [h.color.r, h.color.g, h.color.b, 0],
          outline: {
            // autocasts as new SimpleLineSymbol()
            color: [h.color.r, h.color.g, h.color.b, h.color.a],
            width: 1,
          },
        };
        esri.mapView.graphics.add(gra);
      } else {
        esri.mapView.graphics.removeAll();
      }
    });

    esri.mapView.popup.watch('visible', function(vis) {
      if (!vis) {
        esri.mapView.graphics.removeAll();
      }
    });

    esri.mapView.watch('scale', (value) => {
      if (value > 50000) {
        this.mapScale = true;
      } else {
        this.mapScale = false;
      }
    });

    //add action button to toggle wetlands layer
    const layerButton = document.createElement('button');
    layerButton.id = 'layer-button';
    layerButton.innerHTML = 'Toggle Marshes';
    esri.mapView.ui.add(layerButton, 'bottom-left');

    layerButton.addEventListener('click', () => {
      let layer = esri.map.layers.items.find((layer) => {
        return (
          layer.type == 'map-image' &&
          layer.url ==
            'https://services2.coastalresilience.org/arcgis/rest/services/Chesapeake/MarshManagement/MapServer'
        );
      });

      let sublayer = layer.allSublayers.items.find((sublayer) => {
        return sublayer.id == 28;
      });

      if (sublayer.visible == true) {
        sublayer.visible = false;
      } else {
        sublayer.visible = true;
      }
    });

    //add scalebar widget
    let scaleBar = new ScaleBar({
      view: esri.mapView,
      unit: 'dual',
    });
    esri.mapView.ui.add(scaleBar, {
      position: 'bottom-right',
    });

    //add measure tools
    esri.measurement = new Measurement({
      view: esri.mapView,
    });
    esri.mapView.ui.add(esri.measurement, 'top-left');

    // create legend widget
    esri.legend = new Legend({
      view: esri.mapView,
    });

    // create expand widget to hide and show legend
    esri.lgExpand = new Expand({
      view: esri.mapView,
      content: esri.legend,
    });

    // add expand to map
    esri.mapView.ui.add(esri.lgExpand, 'bottom-left');
    // show expanded legend on desktop, collapse on mobile
    this.$q.screen.lt.sm || this.$q.screen.lt.md
      ? true
      : esri.lgExpand.expand();

    //watch size of map view and adjust legend to close if map gets too small
    esri.mapView.on('resize', function(event) {
      if (event.width < 546) {
        esri.lgExpand.collapse();
      }
      if (event.width > 546) {
        esri.lgExpand.expand();
      }
    });

    // close expand when basemap is changed
    // esri.map.watch('basemap.title', function() {
    //   bgExpand.collapse();
    // });

    // move zoom controls to top right
    esri.mapView.ui.move(['zoom'], 'top-right');
    esri.graphicsLayer = new GraphicsLayer();
    esri.map.add(esri.graphicsLayer);
    esri.sketch = new Sketch({
      layer: esri.graphicsLayer,
      view: esri.mapView,
      // graphic will be selected as soon as it is created
      creationMode: 'update',
      container: 'sketchTools',
      availableCreateTools: ['polygon'],
      //creationMode: 'single',
    });
    esri.sketch.visible = false;
    esri.sketch.visibleElements = {
      // createTools: {
      //   point: false,
      //   circle: false,
      // },
      selectionTools: {
        'lasso-selection': false,
        'rectangle-selection': false,
      },
      settingsMenu: false,
    };
  },

  methods: {
    layerVisibility() {
      this.layerVis[0].forEach((a) => {
        let sublayer = esri.layers.findSublayerById(a);
        sublayer.visible = this.layerVis[1];
      });
    },

    getMapPrint() {
      esri.mapView
        .takeScreenshot({
          width: 750,
          height: 400,
        })
        .then((screenshot) => {
          let image = screenshot.dataUrl;
          let value = Math.round(esri.mapView.scale);
          let scVa = '1:' + value.toLocaleString('en-US');
          this.$store.commit('updateMapPrintURI', image);
          this.$store.commit('updateScaleValue', scVa);
        });
    },

    // Functions for shapefile upload
    generateFeatureCollection() {
      const fileName = this.uploadFileName;
      const portalUrl = 'https://tnc.maps.arcgis.com';
      let name = fileName.split('.');
      // Chrome adds c:fakepath to the value - we need to remove it
      name = name[0].replace('c:\\fakepath\\', '');
      esri.shapefileName = name;
      //document.getElementById('upload-status').innerHTML =
      //  '<b>Loading </b>' + name;

      // define the input params for generate see the rest doc for details
      // https://developers.arcgis.com/rest/users-groups-and-items/generate.htm
      const params = {
        name: name,
        targetSR: esri.mapView.spatialReference,
        maxRecordCount: 1000,
        enforceInputFileSizeLimit: true,
        enforceOutputJsonSizeLimit: true,
      };

      // generalize features to 10 meters for better performance
      params.generalize = true;
      params.maxAllowableOffset = 10;
      params.reducePrecision = true;
      params.numberOfDigitsAfterDecimal = 0;

      const myContent = {
        filetype: 'shapefile',
        publishParameters: JSON.stringify(params),
        f: 'json',
      };

      // use the REST generate operation to generate a feature collection from the zipped shapefile
      esriRequest(portalUrl + '/sharing/rest/content/features/generate', {
        query: myContent,
        body: document.getElementById('uploadForm'),
        responseType: 'json',
      })
        .then((response) => {
          //const layerName =
          //  response.data.featureCollection.layers[0].layerDefinition.name;
          //document.getElementById('upload-status').innerHTML =
          //  '<b>Loaded: </b>' + layerName;
          this.addShapefileToMap(response.data.featureCollection);
        })
        .catch(this.errorHandler);
    },
    addShapefileToMap(featureCollection) {
      // add the shapefile to the map and zoom to the feature collection extent
      // if you want to persist the feature collection when you reload browser, you could store the
      // collection in local storage by serializing the layer using featureLayer.toJson()
      // see the 'Feature Collection in Local Storage' sample for an example of how to work with local storage
      let sourceGraphics = [];

      esri.shapefileLayers = featureCollection.layers.map((layer) => {
        const graphicsObject = layer.featureSet.features.map((feature) => {
          return Graphic.fromJSON(feature);
        });
        let sym = {
          type: 'simple',
          symbol: {
            type: 'simple-fill', // autocasts as new SimpleFillSymbol()
            color: {
              r: 51,
              g: 51,
              b: 204,
              a: 0,
            },
            outline: {
              // autocasts as new SimpleLineSymbol()
              color: [5, 5, 5, 1],
              width: '2 px',
            },
          },
        };

        sourceGraphics = sourceGraphics.concat(graphicsObject);

        const featureLayer = new FeatureLayer({
          objectIdField: 'FID',
          source: graphicsObject,
          renderer: sym,
          fields: layer.layerDefinition.fields.map((field) => {
            return Field.fromJSON(field);
          }),
          title: esri.shapefileName,
        });
        esri.mapView.goTo(graphicsObject);
        this.queryGrid(graphicsObject[0].geometry);
        return featureLayer;
      });

      esri.map.addMany(esri.shapefileLayers);
    },
    queryGrid(geometry) {
      esri.grid
        .queryFeatures({
          geometry: geometry,
          spatialRelationship: 'intersects',
          returnGeometry: false,
          outFields: ['*'],
        })
        .then((featureSet) => {
          this.queryDatasets = true;
          const featureLength = featureSet.features.length;
          // Check the length of returned cell selection
          if (featureLength > 19999) {
            this.largeArea = true;
            this.reportReady = false; // This also keeps the continue button disabled
            this.creatingReport = false;
          } else if (featureLength == 1) {
            this.smallArea = true;
            this.reportReady = false;
            this.creatingReport = false;
          } else if (featureLength <= 0) {
            this.outsideArea = true;
            this.reportReady = false;
            this.creatingReport = false;
          } else {
            let nuisance = 0;
            let impervious = 0;
            let ditches = 0;
            let erosion = 0;
            let sediment = 0;
            let waterQuality = 0;
            let frag = 0;
            let slope = 0;
            let marina = 0;
            let bulkhead = 0;
            let horizShorePos = 0;
            let poolsPonds = 0;
            let terrapin = 0;
            let sav = 0;
            let sparrow = 0;
            let oyster = 0;
            let invasiveSpp = 0;
            let elevVuln = 0;
            featureSet.features.forEach((f) => {
              if (f.attributes.NuisanceFlooding == 'Y') {
                nuisance += 1;
              }
              if (f.attributes.Impervious == 'Y') {
                impervious += 1;
              }
              if (f.attributes.Ditches == 'Y') {
                ditches += 1;
              }
              if (f.attributes.Erosion == 'Y') {
                erosion += 1;
              }
              if (f.attributes.Sediment == 'Y') {
                sediment += 1;
              }
              if (f.attributes.WaterQuality == 'Y') {
                waterQuality += 1;
              }
              if (f.attributes.Fragmentation == 'Y') {
                frag += 1;
              }
              if (f.attributes.Slope == 'Y') {
                slope += 1;
              }
              if (f.attributes.Marina == 'Y') {
                marina += 1;
              }
              if (f.attributes.Bulkhead == 'Y') {
                bulkhead += 1;
              }
              if (f.attributes.ShorelineChange == 'Y') {
                horizShorePos += 1;
              }
              if (f.attributes.MarshDensity == 'Y') {
                poolsPonds += 1;
              }
              if (f.attributes.InvasiveSpecies == 'Y') {
                invasiveSpp += 1;
              }
              if (f.attributes.SaltmarshSparrow == 'Y') {
                sparrow += 1;
              }
              if (f.attributes.Oysters == 'Y') {
                oyster += 1;
              }
              if (f.attributes.TerrapinHabitat == 'Y') {
                terrapin += 1;
              }
              if (f.attributes.SAV == 'Y') {
                sav += 1;
              }
              if (f.attributes.Elevation == 'Y') {
                elevVuln += 1;
              }
            });
            this.creatingReport = true;
            this.largeArea = false;
            if (nuisance > 0) {
              this.$store.commit('updateIndicators', [
                'nuisanceFlooding',
                'Yes',
              ]);
            } else {
              this.$store.commit('updateIndicators', [
                'nuisanceFlooding',
                'No',
              ]);
            }

            if (impervious > 0) {
              if (impervious / featureLength > 0.455) {
                this.$store.commit('updateIndicators', [
                  'imperviousPercent',
                  'Yes',
                ]);
              } else {
                this.$store.commit('updateIndicators', [
                  'imperviousPercent',
                  'No',
                ]);
              }
            } else {
              this.$store.commit('updateIndicators', [
                'imperviousPercent',
                'No',
              ]);
            }

            if (ditches > 0) {
              this.$store.commit('updateIndicators', ['ditchMetric', 'Yes']);
            } else {
              this.$store.commit('updateIndicators', ['ditchMetric', 'No']);
            }

            if (erosion > 0) {
              this.$store.commit('updateIndicators', ['cviHigh', 'Yes']);
            } else {
              this.$store.commit('updateIndicators', ['cviHigh', 'No']);
            }

            if (sediment > 0) {
              this.$store.commit('updateIndicators', ['tssValue', 'Yes']);
            } else {
              this.$store.commit('updateIndicators', ['tssValue', 'No']);
            }

            if (waterQuality > 0) {
              this.$store.commit('updateIndicators', ['waterPolluted', 'Yes']);
            } else {
              this.$store.commit('updateIndicators', ['waterPolluted', 'No']);
            }

            if (frag > 0) {
              this.$store.commit('updateIndicators', ['fragValue', 'Yes']);
            } else {
              this.$store.commit('updateIndicators', ['fragValue', 'No']);
            }

            if (slope > 0) {
              this.$store.commit('updateIndicators', ['slopePercent', 'Yes']);
            } else {
              this.$store.commit('updateIndicators', ['slopePercent', 'No']);
            }

            if (marina > 0) {
              if (bulkhead > 0) {
                this.$store.commit('updateIndicators', [
                  'marinaStructure_Yes_Bulkhead_Yes',
                  'Yes',
                ]);
              } else {
                this.$store.commit('updateIndicators', [
                  'marinaStructure_Yes_Bulkhead_No',
                  'Yes',
                ]);
              }
            } else {
              if (bulkhead > 0) {
                this.$store.commit('updateIndicators', [
                  'marinaStructure_No_Bulkhead_Yes',
                  'Yes',
                ]);
              } else {
                this.$store.commit('updateIndicators', [
                  'marinaStructure_No_Bulkhead_No',
                  'No',
                ]);
              }
            }

            if (poolsPonds > 0) {
              this.$store.commit('updateIndicators', ['uvvrValue', 'Yes']);
            } else {
              this.$store.commit('updateIndicators', ['uvvrValue', 'No']);
            }

            if (horizShorePos > 0) {
              this.$store.commit('updateIndicators', ['erosionHigh', 'Yes']);
            } else {
              this.$store.commit('updateIndicators', ['erosionHigh', 'No']);
            }

            if (invasiveSpp > 0) {
              this.$store.commit('updateIndicators', [
                'phragmitesPresent',
                'Yes',
              ]);
            } else {
              this.$store.commit('updateIndicators', [
                'phragmitesPresent',
                'No',
              ]);
            }

            if (sparrow > 0) {
              this.$store.commit('updateIndicators', ['priorityArea', 'Yes']);
            } else {
              this.$store.commit('updateIndicators', ['priorityArea', 'No']);
            }

            if (oyster > 0) {
              this.$store.commit('updateIndicators', ['oystersPresent', 'Yes']);
            } else {
              this.$store.commit('updateIndicators', ['oystersPresent', 'No']);
            }

            if (terrapin > 0) {
              this.$store.commit('updateIndicators', [
                'terrapinHabitat',
                'Yes',
              ]);
            } else {
              this.$store.commit('updateIndicators', ['terrapinHabitat', 'No']);
            }

            if (sav > 0) {
              this.$store.commit('updateIndicators', ['savPresent', 'Yes']);
            } else {
              this.$store.commit('updateIndicators', ['savPresent', 'No']);
            }

            if (elevVuln > 0) {
              this.$store.commit('updateIndicators', [
                'elevVulnerability',
                'Yes',
              ]);
            } else {
              this.$store.commit('updateIndicators', [
                'elevVulnerability',
                'No',
              ]);
            }

            this.reportReady = true;
            esri.sketch.visible = false;
            this.$store.dispatch('countTechniques');
          }
        });
    },
    clearShapefilePoly() {
      esri.map.removeMany(esri.shapefileLayers);
      esri.mapView.goTo({
        center: [-75.9639, 38.25207],
        zoom: 8,
      });
    },
    sketchTools() {
      esri.sketch.visible = true;
      //event for on draw complete - only need to run once
      if (esri.createEvent == false) {
        esri.sketch.on('create', (event) => {
          if (event.state === 'complete') {
            this.showStatus = true;
            // This is where you call the function when the user completes their sketch
            esri.mapView.goTo(esri.graphicsLayer.graphics.items[0]);
            this.queryGrid(esri.graphicsLayer.graphics.items[0].geometry);
          }
        });
        esri.sketch.on('update', (event) => {
          if (event.state === 'complete' && !event.aborted) {
            this.showStatus = true;
            // This is for when the user updates the drawn shape
            esri.mapView.goTo(esri.graphicsLayer.graphics.items[0]);
            this.queryGrid(esri.graphicsLayer.graphics.items[0].geometry);
          } else if (event.aborted) {
            this.showStatus = false;
          }
        });
        esri.createEvent = true;
      }
    },
    clearSketch() {
      esri.graphicsLayer.removeAll();
      esri.sketch.visible = false;
    },
    showSupportingLayers() {
      esri.supportingLayersExpand.expand();
    },
  },
};
</script>

