<template>
  <div>
    <q-checkbox
      :disable="dis"
      v-model="val"
      @update:model-value="layerVisible"
    />
  </div>
</template>
<script>
export default {
  props: ['layerId', 'dis'],
  data() {
    return {
      val: false,
    };
  },
  computed: {
    layerVis: {
      get() {
        return this.$store.state.layerVis;
      },
      set(value) {
        this.$store.commit('updateLayerVis', value);
      },
    },
  },
  methods: {
    layerVisible() {
      this.layerVis = [this.layerId, this.val];
    },
  },
};
</script>
