<template>
  <div style="padding: 20px 0 50px 0;">
    <div class="text-h5" style="text-align: center;">Define Area</div>

    <div class="q-pa-md">
      <q-btn-toggle
        spread
        v-model="shapeType"
        @update:model-value="this.toggleClick"
        toggle-color="primary"
        :options="[
          { label: 'Upload Shapefile', value: 'upload' },
          { label: 'Draw Area', value: 'draw' },
        ]"
      />
    </div>
    <div v-if="shapeType === 'upload'" class="secondary-button">
      <div>
        To upload a shapefile of your area, click the "Upload" button and select
        a
        <strong>zipped</strong> shapefile from the file explorer.
      </div>
      <q-btn
        @click="this.startUpload"
        label="Upload"
        color="primary"
        style="margin-top: 10px;"
      ></q-btn>
      <form
        enctype="multipart/form-data"
        method="post"
        id="uploadForm"
        @change="sendUpload"
      >
        <input type="file" name="file" id="inFile" hidden />
      </form>
    </div>
    <div v-if="shapeType === 'draw'" class="secondary-button">
      <div>
        Click the polygon icon in the draw tools at the top center of the map to
        create your work area.
      </div>
    </div>
    <div v-if="showStatus">
      <q-list bordered separator>
        <q-item>
          <q-item-section avatar v-if="!queryDatasets">
            <q-icon color="primary" name="fas fa-spinner fa-spin-pulse" />
          </q-item-section>
          <q-item-section avatar v-if="queryDatasets">
            <q-icon color="green" name="fas fa-circle-check" />
          </q-item-section>
          <q-item-section>Querying datasets</q-item-section>
        </q-item>
        <q-item>
          <q-item-section avatar v-if="!creatingReport && !queryDatasets">
            <q-icon color="primary" name="fas fa-spinner fa-spin-pulse" />
          </q-item-section>
          <q-item-section avatar v-if="creatingReport">
            <q-icon color="green" name="fas fa-circle-check" />
          </q-item-section>
          <q-item-section avatar v-if="largeArea || smallArea || outsideArea">
            <q-icon color="red" name="fas fa-circle-xmark" />
          </q-item-section>
          <q-item-section>Creating Report</q-item-section>
        </q-item>
        <q-item>
          <q-item-section avatar v-if="!reportReady && !queryDatasets">
            <q-icon color="primary" name="fas fa-spinner fa-spin-pulse" />
          </q-item-section>
          <q-item-section avatar v-if="reportReady">
            <q-icon color="green" name="fas fa-circle-check" />
          </q-item-section>
          <q-item-section avatar v-if="largeArea || smallArea || outsideArea">
            <q-icon color="red" name="fas fa-circle-xmark" />
          </q-item-section>
          <q-item-section>Report Ready</q-item-section>
        </q-item>
        <q-item v-if="reportReady">
          <q-item-section style="text-align:center">
            Click Continue below to view Report</q-item-section
          >
        </q-item>
      </q-list>
      <div v-if="showRemove">
        <q-btn
          class="upload-btn"
          @click="this.removeUpload"
          label="Remove Area"
          color="negative"
        ></q-btn>
      </div>
      <div v-if="largeArea">
        <q-card class="bg-red-3">
          <q-card-section>
            The drawn or uploaded area is larger than allowed. Please redraw or
            upload a new area.
          </q-card-section>
        </q-card>
      </div>
      <div v-if="smallArea">
        <q-card class="bg-red-3">
          <q-card-section>
            The drawn or uploaded area is smaller than allowed. Please redraw or
            upload a new area.
          </q-card-section>
        </q-card>
      </div>
      <div v-if="outsideArea">
        <q-card class="bg-red-3">
          <q-card-section>
            The drawn or uploaded area is outside of the intended area for the
            tool (Chesapeake Bay marshes). Please redraw or upload a new area.
          </q-card-section>
        </q-card>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      shapeType: '',
    };
  },
  methods: {
    startUpload() {
      this.userSelection = 'shapefile';
      document.getElementById('inFile').click();
    },
    removeUpload() {
      this.$store.commit('updateClearShapefile');
      this.$store.commit('updateStartSketch');
      this.shapeType = '';
      this.showRemove = false;
      this.showStatus = false;
      this.smallArea = false;
      this.largeArea = false;
      this.outsideArea = false;
    },
    toggleClick() {
      if (this.shapeType === 'draw') {
        this.$store.commit('updateStartSketch', true);
      }
    },
    sendUpload(event) {
      const fileName = event.target.value.toLowerCase();
      //TODO ADD ERROR HANDLING IF NOT ZIP
      if (fileName.indexOf('.zip') !== -1) {
        //is file a zip - if not notify user
        this.uploadFileName = fileName;
      } else {
        document.getElementById('upload-status').innerHTML =
          '<p style="color:red">Add shapefile as .zip file</p>';
      }
      this.showRemove = true;
      this.showStatus = true;
    },
  },
  computed: {
    uploadFileName: {
      get() {
        return this.$store.state.uploadFileName;
      },
      set(value) {
        this.$store.commit('updateUploadFileName', value);
      },
    },
    queryDatasets() {
      return this.$store.state.queryDatasets;
    },
    creatingReport() {
      return this.$store.state.creatingReport;
    },
    reportReady() {
      return this.$store.state.reportReady;
    },
    showStatus: {
      get() {
        return this.$store.state.showStatus;
      },
      set(value) {
        this.$store.commit('updateShowStatus', value);
      },
    },
    showRemove: {
      get() {
        return this.$store.state.showStatus;
      },
      set(value) {
        this.$store.commit('updateShowRemove', value);
      },
    },
    largeArea: {
      get() {
        return this.$store.state.largeArea;
      },
      set(value) {
        this.$store.commit('updateLargeArea', value);
      },
    },
    smallArea: {
      get() {
        return this.$store.state.smallArea;
      },
      set(value) {
        this.$store.commit('updateSmallArea', value);
      },
    },
    outsideArea: {
      get() {
        return this.$store.state.outsideArea;
      },
      set(value) {
        this.$store.commit('updateOutsideArea', value);
      },
    },
  },
};
</script>

<style scoped>
#icon {
  color: white;
}
.q-uploader__list {
  display: none;
}
.q-uploader {
  box-shadow: none;
}
.q-uploader__subtitle {
  display: none !important;
}
.upload-btn {
  margin: 10px auto;
  display: flex;
}
.secondary-button {
  margin: 10px;
  text-align: center;
}
</style>
